.about {
  background-color: #f5f5f5;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: flex-start;

  padding-top: 3vw;

  min-height: 100vh;
}

.about p {
  max-width: 50vw;
  font-size: calc(16px + 1vw);
  margin: 1vw;
  padding: 1vw;
  text-align: center;
  line-height: 1.7;
}

@media screen and (max-width: 1024px) {
  .about {
    display: flex;
    flex-direction: row;
    width: 100%;

    padding: 0;
    padding-top: 15vw;

    margin: 0;
  }

  .about p {
    max-width: 66vw;
    font-size: 4vw;

    font-size: calc(16px + 1.5vw);

    text-align: center;

    margin: 1vw;
    padding: 3vw;
  }
}
